<template>
  <div id="app"><!-- template下只能有一个子标签，该子标签下可以有任意个孙标签  -->
    <!--@go_home为子组件headPage的自定义事件，事件名为go_home。绑定的函数为父组件的函数gohome，从而可以通过在子组件中激发go_home事件时，调用父组件的函数  -->
    <headPage @go_home="gohome"></headPage>
    <router-view/>
    <tailPage></tailPage>
  </div>
</template>

<script>
    //App.vue 是项目入口文件
    //引用组件
    import headPage from './components/headPage.vue'
    import tailPage from './components/tailPage.vue'  
    export default {
      name: 'App',
      //把引用的组件给申明到 components 里面去。这样，我们就可以在 template 里面使用了
      components: { 
        tailPage ,  
        headPage 
      }, 
      methods:{ //组件的中的方法，可以在组件中使用，包括在标签中
        gohome(data){//触发子组件
          this.$router.push({ path: '/' });
        }
      }
    }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
</style>



